<template>
  <div class="comment">
    <template v-if="!editMode">
      <p>{{ comment.text }}</p>
      <button @click="toggleEditMode">edit</button>
      <button @click="addComment">comment</button>
      <button @click="deleteParentComment">delete</button>
    </template>

    <template v-else>
      <textarea
          v-model="comment.text"
          ref="commentTextarea"/>
      <button
          @click="toggleEditMode"
          :disabled="!comment.text">ok</button>
    </template>

    <template v-if="comment.children">
      <Comment
          v-for="(comment, key) in comment.children"
          :key="comment.children.length - key"
          :comment="comment"
          @deleteComment="deleteChildComment(key)"
      />
    </template>

  </div>
</template>

<script>
export default {
  name: 'Comment',

  data() {
    return {
      editMode: false
    }
  },

  props: {
    comment: {
      type: Object,
      default: () => {}
    }
  },

  methods: {
    toggleEditMode() {
      this.editMode = !this.editMode
    },

    addComment() {
      this.comment.children.unshift({text: '', children: []})
    },

    deleteParentComment() {
      this.$emit('deleteComment')
    },

    deleteChildComment(index) {
      this.comment.children.splice(index, 1)
    }
  },

  mounted() {
    if (!this.comment.text) {
      this.editMode = true
      this.$nextTick(() => {
        this.$refs.commentTextarea.focus()
      })
    }
  }

}
</script>

<style scoped>
.comment {
  position: relative;
  margin-left: 30px;
}
</style>